.principal-button{
  width: 45px;
  height: 45px;
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.5);
  border-radius: 45px;
  background-color: $colorTertiary;
  margin: 0 auto;
  cursor: pointer;
  display: block;

}

.down{
  background-image: url('../images/ico-scroll-down.svg');
  background-repeat: no-repeat;
  background-position: center;
  position: relative;
 cursor: pointer;
}

.up{
  background-color: $colorTertiary;
  border-radius: 45px;
  background-image: url('../images/ico-arrow.svg');
  background-repeat: no-repeat;
  background-position: center;
  position: relative;
  top: -22px;
}

.button-go{
  width: 76px;
  height: 45px;
  border-radius: 22.5px;
  text-align: center;
  background-color: inherit;
  border: solid 2px $colorSecundary;
  color: $colorSecundary;
  font-family: 'Roboto';
  font-size: 20px;
  line-height: 2.25;
  cursor: pointer;
  transition: background ease 0.5s;

  @media all and (min-width: 768px){
    margin-left: 275;
  }
  @media all and (min-width: 1024px){
    width: 160px;
    &:hover {
      color: $colorTertiary;
      background-color: $colorSecundary;
    }
  }
}

.button-more{
  display: block;
  width: 184px;
  height: 45px;
  padding: 11px 26px;
  border-radius: 22.5px;
  background-color: $colorSecundary;
  color: $colorTertiary;
  font-family: 'Roboto';
  font-size: 20px;
  font-weight: 500;
  margin: 0 auto;
  cursor: pointer;
  transition: background ease 0.5s;
  &:hover {
    background-color: $colorPrimary;
  }
  @media all and (min-width: 768px){
    width: 210px;
  }

  @media all and (min-width: 1024px){
    width: 255px;
  }
}
